namespace $ { export class $mol_clicker extends $mol_viewer {

	/// tagName \button
	tagName() {
		return "button"
	}

	/// enabled true
	enabled() {
		return true
	}

	/// eventClick null
	@ $mol_mem()
	eventClick( next? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// eventActivate > eventClick
	@ $mol_mem()
	eventActivate( next? : any ) {
		return this.eventClick( next )
	}

	/// event * click > eventActivate
	event() {
		return $mol_merge_dict( super.event() , {
			"click" : ( next? : any )=> <any> this.eventActivate( next ) ,
		} )
	}

	/// disabled false
	disabled() {
		return false
	}

	/// attr * 
	/// 	disabled < disabled 
	/// 	tabindex \0
	attr() {
		return $mol_merge_dict( super.attr() , {
			"disabled" : ()=> <any> this.disabled() ,
			"tabindex" : ()=> <any> "0" ,
		} )
	}

} }

